.MobileNav {
    width: 40px;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    padding: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    cursor: pointer;
}

.MobileNav div {
    width: 85%;
    height: 5px;
    background-color: white;
    margin-top: 4px;
}

@media (min-width: 500px) {
    .MobileNav {
        display: none;
    }
}


