.Toolbar{
    height: 56px;
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    background-color: #33DDCC;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav{
    height: 100%;
}

.Logo{
    height: 150%;
    margin-top:2.9%;
    margin-left: 5%;
}

@media (max-width: 499px){
    .DesktopOnly{
        display: none;
    }
}
@media (min-width: 500px){
    .MobileOnly{
        display: none;
    }
}


